.address__wrapper {
  max-width: 1160px;
  margin: 0 auto;
  padding: 120px 0;
  display: grid;
  grid-template-columns: minmax(324px, 560px) minmax(324px, 500px);
  column-gap: 100px;

  @include vp-1023 {
    padding: 100px 0;
    column-gap: 30px;
  }

  @include vp-767 {
    padding: 58px 0 50px;
    grid-template-columns: 1fr;
    row-gap: 34px;
  }
}

.address__map {
  width: 100%;
  height: 306px;
  display: flex;
  align-items: center;

  @include vp-1023 {
    height: 335px;
    align-self: center;
  }

  @include vp-767 {
    grid-row: 2 / 3;
  }
}

.address__map img {
  width: 100%;
}

.address__description {
  display: flex;
  flex-direction: column;
  justify-content: center;

  @include vp-1023 {
    display: block;
    padding-top: 66px;
  }

  @include vp-767 {
    display: flex;
    padding-top: 0;
    grid-row: 1 / 2;
  }
}

.address__description-heading {
  margin: 0 0 30px;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;

  @include vp-1023 {
    margin-bottom: 32px;
  }

  @include vp-767 {
    margin-bottom: 32px;
  }
}

.address__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.address__item {
  margin-bottom: 22px;
  display: grid;
  grid-template-columns: auto 1fr;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.address__text-wrap {
  margin: 0;
  display: grid;
  grid-template-columns: 18px 1fr;
  column-gap: 10px;
  align-items: center;

  @include vp-1023 {
    align-items: start;
  }
}

.address__item a {
  @include hover {
    outline: 1px solid $color-text;
  }
}

.address__item svg {
  margin-right: 10px;
}

.address__description-text {
  font-size: 16px;
  line-height: 1.5;
  vertical-align: super;

  @include vp-1023 {
    margin-top: -4px;
  }
}

.address__description-text br {
  @media (min-width: $vp-1024) {
    display: none;
  }
}

// Скрываем копирайты у яндекс карты

[class*="copyrights-pane"] {
  // display: none !important;
  display: none;
}

