.social {
  margin-bottom: 10px;

  @include vp-767 {
    margin-left: -4px;
    align-self: start;
  }
}

.social__network {
  margin: 0;
  padding: 0;
  list-style-type: none;

  display: grid;
  grid-template-columns: repeat(auto-fit, 20px);
  gap: 27px;
  justify-content: end;

  @include vp-1023 {
    gap: 18px;
  }

  @include vp-767 {
    display: flex;
    flex-wrap: wrap;
    gap: 0;
  }
}

.social__network-item {
  margin-right: 26px;

  &:last-of-type {
    margin-right: 0;
  }

  @include vp-1023 {
    margin-right: 22px;
  }

  @include vp-767 {
    margin-right: 20px;
  }
}

.social__network-item a {
  @include hover {
    outline: 1px solid $color-default-white;
  }
}

.social__network-item svg {
  @include vp-1023 {
    width: 16px;
    height: 16px;
  }
}
